    <div class="advantage-content">
<!--      <div class="advantage-content-left">-->
<!--        2014<br/>-->
<!--        FBA5108000<br/>-->
<!--        280000+Fedex<br/>-->
<!--        FBA FBAFBA-->
<!--      </div> -->
      <div class="advantage-content-left">
        <div class="content">
        2014<br/>
        FBA5108000<br/>
        280000+
        </div>
        <input type="submit" class="main-btn" value="" >
      </div>

      <div class="advantage-content-right">
<!--        <img src="../assets/img11.png" style="width: 90%;">-->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "AboutUsInfo"
}
