<style scoped>

.advantage-title .main-title {
  letter-spacing: 15px;
  color: #409eff;
  text-transform: uppercase;
  padding: 20px 0px;
  font-weight: bold;
  font-size: 28px;
  font-family: 'Microsoft YaHei', 'serif';
  text-align: center;
  margin-bottom: 10px;
  margin-top: 0;
}

.main-btn {
  display: inline-block;
  position: relative;
  margin: 35px 0px;
  min-width: 115px;
  min-height: 45px;
  outline: none;
  border: 0;
  border-radius: 100px;
  background-color: #409eff;
  color: white;
  cursor: pointer;
  text-align: center;
  line-height: 45px;
  -webkit-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
  font-size: 16px;
}

.advantage-content {
  font-size: 0;
  display: flex;
  flex-flow: row wrap;
  margin-top: 20px;
  padding: 20px;
}

.advantage-content-left {
  width: 100%;
}

.advantage-content-right {
