  background: url("../assets/updateimg222.png") no-repeat center;
  background-size: contain;
  height: 200px;
}

.advantage-content-left .content {
  font-size: 16px;
  text-align: left;
  color: #4a4a4a;
  letter-spacing: .5px;
  line-height: 30px;
}

/*768*/
@media (min-width: 48em) {
}

/*1024*/
@media (min-width: 64em) {
  .advantage-content-left {
    width: 50%;
  }

  .advantage-content-right {
    width: 50%;
    height: 400px;
  }
}

/*1440*/
@media (min-width: 90em) {
  .advantage-content {
    width: 1300px;
    justify-content: center; /*  */
    margin-right: auto;
    margin-left: auto;
  }

  .advantage-title .main-title {
    font-size: 42px;
  }
}
</style>



