<template>
  <div class="base-info">
    <div class="advantage-title" >
      <h2 class="main-title theme-color"></h2>
    </div>
    <section class="base-info-content">
      <ul>
        <li>
          <div class="advantage-content-left-li">
              <div class="header-feature"></div>
              <p>100%</p>
          </div>
        </li>

        <li>
          <div class="advantage-content-left-li">
              <div  class="header-feature"></div>
              <p>11</p>
          </div>
        </li>

        <li>
          <div class="advantage-content-left-li">
              <div  class="header-feature"></div>
              <p></p>
          </div>
        </li>

        <li>
          <div class="advantage-content-left-li">
              <div  class="header-feature"></div>
              <p></p>
          </div>
        </li>

        <li>
          <div class="advantage-content-left-li">
              <div class="header-feature"></div>
              <p></p>
          </div>
        </li>

        <li>
          <div class="advantage-content-left-li">
              <div class="header-feature"></div>
              <p></p>
          </div>
        </li>

