</template>

<script>
export default {
  name: "AdvantageInfo2"
}
</script>

<style scoped>

.advantage-title .main-title {
  text-transform: uppercase;
  padding: 20px 0px;
  font-size: 28px;
  font-family: 'Microsoft YaHei', 'serif';
  text-align: center;
  margin-bottom: 10px;
  font-weight: bold;
  margin-top: 0;
}

.theme-color {
  color: #409eff;
}

.base-info-content {
  max-width: 1300px;
  margin: 0px auto;
  padding: 30px 10px;
}

.base-info-content ul {
  display: flex;
  flex-flow: row wrap;
  text-align: center;
}

.base-info-content li .header-feature {
  color: #409effE5;
  font-family: 'Impact', 'Arial', 'serif';
  font-size: 28px;
  /*font-weight: bold;*/
  line-height: 1.8;
  margin-bottom: 20px;
  position: relative;
}

.base-info-content li span {
  font-size: 1em;
  font-weight: 700;
  color: #060f28;
  display: block;
  line-height: 1.2;
  overflow: hidden;
  position: relative;
}

.base-info-content li {
  width: 100%;
  padding: 6px 20px;
  text-align: left;
}

.advantage-content-left-li p{
  padding-top: 30px;
  padding-right: 10px;
  border-top: 1px solid #dcdcdc;
  color: #666666;
}

/*576*/
@media (min-width: 36em) {
  .base-info-content li {
    width: 100% !important;
  }
}

/*768*/
@media (min-width: 48em) {
  .base-info-content li {
    width: 50% !important;
    padding-bottom: 40px;
  }
}

/*1024*/
@media (min-width: 64em) {
  .base-info-content li {
    width: 33.33% !important;
  }
  .advantage-title .main-title {
    font-size: 38px;
  }

}

/*1440*/
@media (min-width: 90em) {
  .base-info-content li {
    width: 25% !important;
  }
  .base-info-content li b {
    font-size: 4.5em;
  }
  .advantage-title .main-title {
    font-size: 42px;
  }

}
</style>



